<template>
	<view>

		<uni-nav-bar style="margin-bottom: 20rpx;" @clickLeft="back" left-icon="back" title="收藏题目">
			<template v-if="kongkong == false">
				<view slot="right">
					<text @click="openpicker" class="iconfont icon-caidan1"></text>
					<text>{{parseInt(currentIndex) + 1}}/{{ti_num}}</text>
				</view>
			</template>
		</uni-nav-bar>

		<!-- 刷题内容 每页只有一个题 -->
		<view v-if="tiList[currentIndex] != undefined" class="shuati-wrao" @touchmove="handletouchmove" @touchstart="handletouchstart"
		 @touchend="handletouchend">
			<!-- 题标题及描述部分 -->
			<view class="ti-top-wrap">
				<view class="title">
					<text class="ti-type">{{ti_type[tiList[currentIndex].type]}}</text>
					<text>{{tiList[currentIndex].title}}</text>
				</view>
				<image style="width:200rpx; height:200rpx;" v-if="tiList[currentIndex].answer.t_img != ''" :src="tiList[currentIndex].answer.t_img"
				 mode=""></image>
				<imt-audio class="imtaudio" v-if="tiList[currentIndex].answer.t_audio != ''" :src="tiList[currentIndex].answer.t_audio"></imt-audio>
				<video class="ti-video" v-if="tiList[currentIndex].answer.t_video != ''" :src="tiList[currentIndex].answer.t_video"
				 :poster="tiList[currentIndex].answer.t_video_img" controls></video>
			</view>

			<!-- 选项区 类型 0判断1单选2定项多选3简答4填空5不定项多选 -->
			<!-- 如果是选择题则序号 A B C D 如果填空题则 1 2 3-->
			<view class="ti-info-wrap">

				<!-- 判断题 rs为0错误 1正确 -->
				<block v-if="tiList[currentIndex].type == 0">
					<view @click="checkPanduan(tiList[currentIndex].answer.rs, '1')" class="ti-info-item tiinfo-item-panduan-right">
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="ti-info-item-xuhao">A</text>
						<image v-else class="duicuo-img" :src="getDuiCuoFlag('0','1')" mode="aspectFill"></image>
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="">正确</text>
						<text v-if="user_answer.ti_data[currentIndex] == 'is_end'" :class="panduan.a_class">正确</text>
					</view>
					<view @click="checkPanduan(tiList[currentIndex].answer.rs, '0')" class="ti-info-item tiinfo-item-panduan-wrong">
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="ti-info-item-xuhao">B</text>
						<image v-else class="duicuo-img" :src="getDuiCuoFlag('0', '0')" mode="aspectFill"></image>
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="">错误</text>
						<text v-if="user_answer.ti_data[currentIndex] == 'is_end'" :class="panduan.b_class">错误</text>
					</view>

				</block>

				<!-- 单选题 -->
				<block v-else-if="tiList[currentIndex].type == 1">
					<view @click="checkDanxuan(tiList[currentIndex].answer.rs, index)" v-for="(item, index) in tiList[currentIndex].answer.ans"
					 :key="index" class="ti-info-item">
						<!-- 这里用v-else会报错 -->
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="">{{item}}</text>
						<image v-if="user_answer.ti_data[currentIndex] == 'is_end'" class="duicuo-img" :src="getDuiCuoFlag('1', index)"
						 mode="aspectFill"></image>
						<text v-if="user_answer.ti_data[currentIndex] == 'is_end'" :class="ans_txt_class[index]">{{item}}</text>
					</view>
				</block>

				<!-- 定项选择题 -->
				<block v-else-if="tiList[currentIndex].type == 2">
					<view @click="checkDingxiang(tiList[currentIndex].answer.rs, index)" v-for="(item, index) in tiList[currentIndex].answer.ans"
					 :key="index" class="ti-info-item">
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" :class="{is_checked : (dingxiang.abc[index].is_checked == '1')}"
						 class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
						<block v-else>
							<image v-if="tiList[currentIndex].answer.rs.indexOf(index) != '-1'" class="duicuo-img" :src="duihao" mode="aspectFill"></image>
							<image v-else class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
						</block>
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="">{{item}}</text>
						<block v-if="user_answer.ti_data[currentIndex] == 'is_end'">
							<text v-if="tiList[currentIndex].answer.rs.indexOf(index) != '-1'" class="green">{{item}}</text>
							<text v-else class="red">{{item}}</text>
						</block>
					</view>
					<!-- 没提交时显示 -->
					<view v-if="dingxiang.is_sub != '1'" class="ans-btn-wrap">
						<text @click="subDingXiang" :class="{is_start_check : (dingxiang.is_start_check == '1')}" class="ans-btn">确认答案</text>
					</view>
				</block>

				<!-- 刷题没有简答题 -->
				<!-- 填空题 -->
				<block v-else-if="tiList[currentIndex].type == 4">
					<!-- 根据答案 控制input数量 -->
					<view v-for="(item,index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item ti-info-item-tiankong">
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="ti-info-item-xuhao ti-info-item-xuhao-num">{{parseInt(index)+1}}</text>
						<view v-else>
							<!-- 已做完题 -->
							<image v-if="item[0] == tiankong.ans[index]" class="duicuo-img" :src="duihao" mode="aspectFill"></image>
							<image v-else class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
						</view>

						<input v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="tiankong-input" v-model="tiankong.ans[index]"
						 style="border: 2rpx solid #DDDDDD;" type="text" value="" />
						<input v-else-if="item[0] == tiankong.ans[index]" class="tiankong-input" v-model="tiankong.ans[index]" style="border: 2rpx solid #DDDDDD;color: #38DAA6;"
						 type="text" value="" />
						<input v-else-if="item[0] != tiankong.ans[index]" class="tiankong-input" v-model="tiankong.ans[index]" style="border: 2rpx solid #DDDDDD;color: #FF5050;"
						 type="text" value="" />
					</view>

					<!-- 没提交时显示 -->
					<view v-if="tiankong.sub[tiList[currentIndex].id] != '1'" class="ans-btn-wrap">
						<text @click="subTiankong" :class="{is_start_check : (tiankong.is_start_check == '1')}" class="ans-btn">确认答案</text>
					</view>
				</block>

				<!-- 不定项多选 -->
				<block v-else-if="tiList[currentIndex].type == 5">
					<view @click="checkBudingXiang(tiList[currentIndex].answer.rs, index)" v-for="(item, index) in tiList[currentIndex].answer.ans"
					 :key="budingxiang.key[index]" class="ti-info-item">
						<!-- 没做完时 -->
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" :class="{is_checked : (budingxiang.abc[tiList[currentIndex].id][index].is_checked == '1')}"
						 class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
						<block v-else>
							<image v-if="tiList[currentIndex].answer.rs.indexOf(index) != '-1'" class="duicuo-img" :src="duihao" mode="aspectFill"></image>
							<image v-else class="duicuo-img" :src="cuohao" mode="aspectFill"></image>
						</block>
						<text v-if="user_answer.ti_data[currentIndex] != 'is_end'" class="">{{item}}</text>
						<block v-if="user_answer.ti_data[currentIndex] == 'is_end'">
							<text v-if="tiList[currentIndex].answer.rs.indexOf(index) != '-1'" class="green">{{item}}</text>
							<text v-else class="red">{{item}}</text>
						</block>
					</view>
					<!-- 没提交时显示 -->
					<view v-if="budingxiang.sub[tiList[currentIndex].id] != '1'" class="ans-btn-wrap">
						<text @click="subBudingXiang" :class="{is_start_check : (budingxiang.is_start_check == '1')}" class="ans-btn">确认答案</text>
					</view>
				</block>

				<!-- 答案和解析 -->
				<view v-if="user_answer.ti_data[currentIndex] == 'is_end'" class="ans-jiexi">
					<view class="ans">
						<text style="font-weight: bold; margin-right: 10rpx;">答案</text>
						<text v-if="tiList[currentIndex].type == '0'">{{tiList[currentIndex].answer.rs == '1'  ? 'A' : 'B'}}</text>
						<text v-if="tiList[currentIndex].type == '1'">{{ti_info_item_abc[tiList[currentIndex].answer.rs]}}</text>
						<text v-if="tiList[currentIndex].type == '2'">{{dingxiang.right_ans}}</text>
						<text v-if="tiList[currentIndex].type == '4'">{{tiankong.right_ans[currentIndex]}}</text>
						<text v-if="tiList[currentIndex].type == '5'">{{budingxiang.right_ans[currentIndex]}}</text>

					</view>
					<view class="jiexi">
						<view class="jiexi-title">解析</view>
						<view class="jiexi-content">
							{{tiList[currentIndex].parsing}}
						</view>
					</view>
				</view>

			</view>

		</view>

		<!-- 收藏 对错区 -->
		<view class="star-right-wrong-wrap">
			<template v-if="kongkong == false">
				<view @click="storeQuestion(tiList[currentIndex].testid, tiList[currentIndex].qid)" class="bottom-nav-left bottom-nav-item">
					<block v-if="storeData[currentIndex] === true">
						<text class="iconfont icon-pingfenxingxing xingxing_checked"></text>
						<text class="score-txt">已收藏</text>
					</block>

					<block v-else>
						<text class="iconfont icon-pingfenxingxing"></text>
						<text class="score-txt">收藏</text>
					</block>
				</view>

				<view class="srww-item dui-tongji">
					<image style="display: none;" class="tongji-img" src="../../static/images/tongji_duihao.png" mode="aspectFill"></image>
					<text style="display: none;">{{user_answer.dui_num}}</text>
				</view>
				<view class="srww-item cuo-tongji">
					<image style="display: none;" class="tongji-img" src="../../static/images/tongji_cuohao.png" mode="aspectFill"></image>
					<text style="display: none;">{{user_answer.cuo_num}}</text>
				</view>
			</template>
			<!-- 只有此题做错了,才显示存入错题本按钮 -->
			<view @click="addCuotiben(tiList[currentIndex].testid, tiList[currentIndex].qid)" class="srww-item cuotiben">

				<text v-if="right_cuo[currentIndex] == '0'">存入错题本</text>
				<text v-else-if="right_cuo[currentIndex] == '1'">已存入错题本</text>
			</view>

		</view>


		<!-- 遮罩区域 -->
		<!-- 遮罩上层 -->
		<view @click="closezhetop" v-if="is_zhe_top == '1'" class="zhetop">
		</view>

		<view v-if="is_zhe_show == '1'" class="zhe">
			<view class="zhe-title">题目列表</view>
			<!-- 题号区域 -->
			<view class="tihao-wrap">
				<block v-for="(item,index) in ti_num" v-if="right_cuo[index] == '1'">
					<text @click="checkByTihao(index)" class="tihao-item tihao_right">{{parseInt(index)+1}}</text>
				</block>
				<block v-else-if="right_cuo[index] == '0'">
					<text @click="checkByTihao(index)" class="tihao-item tihao_wrong">{{parseInt(index)+1}}</text>
				</block>
				<block v-else>
					<text @click="checkByTihao(index)" class="tihao-item">{{parseInt(index)+1}}</text>
				</block>
			</view>
		</view>

		<template v-if="kongkong == true">
			<view :class="{xiangziwrap : (kongkong == true)}">
				<image class="xiangzi" src="../../static/images/xiangzi.png" mode="aspectFill"></image>
				<text class="xiangzi_txt">还未收藏题目</text>
			</view>
		</template>

	</view>
</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
	import imtAudio from '@/components/imt-audio/imt-audio.vue';

	const app = getApp();

	export default {
		components: {
			uniNavBar,
			imtAudio
		},
		data() {
			return {
				flag: 0, //1向左滑动,2向右滑动,3向上滑动 4向下滑动
				text: '', //向哪里滑动
				lastX: 0,
				lastY: 0,
				index: 0,
				ti_info_item_abc: ['A', 'B', 'C', 'D', 'E'], //选择题选项
				tiku_ids: [],
				ti_num: 0,
				tiList: {}, //试题数组
				testsid: 0, //试题id 刷题时收藏题目 试题id为0
				currentIndex: '0', //当前页面索引
				currentQid: '1', //当前题号 (索引+1)
				// 0判断1单选2定项多选3简答4填空5不定项多选
				ti_type: ['判断', '单选', '定项多选', '简答', '填空', '不定项多选'],
				poster: '', //视频封面
				user_answer: {
					'dui_num': 0,
					'cuo_num': 0,
					'ti_data': []
				},
				duihao: '../../static/images/tongji_duihao.png',
				cuohao: '../../static/images/tongji_cuohao.png',
				ans_txt_class: {
					'0': '',
					'1': '',
					'2': '',
					'3': ''
				}, //选项文字颜色类 绿正确 红错误
				//判断题相关数据
				panduan: {
					'is_end': '0', //是否已做完此题(记入缓存使用)
					'a_class': '', //A选项样式
					'b_class': '', //B选项样式
				},
				ansCache: null, //用户答题缓存数据,
				// 定项选择题相关数据
				dingxiang: {
					'ans': [], //存放每次点击答案时的选项id
					'abc': [{
						'is_checked': '0'
					}, {
						'is_checked': '0'
					}, {
						'is_checked': '0'
					}, {
						'is_checked': '0'
					}, {
						'is_checked': '0'
					}],
					'is_start_check': '', //是否已经开始选择 是 则确认答案按钮变绿
					'is_sub': '0', //是否提交
					'right_ans': '', //正确答案字符串 A B C..
				},
				// 填空题相关数据
				tiankong: {
					'ans': [], //存放用户input输入的答案
					'sub': [], //是否提交 id: 是否提交 {'1' : '1', '2': '1', ...}
					'right_ans': [], //正确答案字符串 1.XX 2.XX
				},
				// 不定项选择题相关数据
				budingxiang: {
					'ans': [], //存放用户每次点击答案时的选项id
					'abc': [], //{'is_checked': '0'}, 是否已经选择
					'is_start_check': '', //是否已经开始选择 是 则确认答案按钮变绿
					'sub': [], //是否提交 题号:是否提交 {'1' : '1', '2': '1', ...}
					'right_ans': [], //正确答案数组字符串[id1: 'A B C', id2: 'A B C D'] 
					'key': {
						'0': '0',
						'1': '1',
						'2': '2',
						'3': '3'
					}, //占位key, 强制组件重新渲染
				},
				storeData: [], //收藏题目相关数据 题号 => (true已收藏 false未收藏)
				is_zhe_show: '0', //是否显示遮罩层
				is_zhe_top: '0', //是否显示遮罩层上半边
				cuotibenData: [], //错题本相关数据 题号 => (true已存入错题本 false未存入错题本)
				right_cuo: [], // //索引 对 1 错 0
				user_ans: [], //存放用户每题答案 索引 => res
				userinfo: '', //用户信息
				kongkong: false
			}
		},
		onLoad(option) {

			let userInfo = uni.getStorageSync('userinfo');
			if (!userInfo) {
				uni.navigateTo({
					url: '../login/login',
				});
			}
			this.userinfo = userInfo;

			uni.setStorageSync('user_answer', this.user_answer);
			// 获取收藏列表
			this.getStoreList();
			this.ansCache = uni.getStorageSync('user_answer'); //用户答案
		},
		methods: {

			// 获取所有题目
			getTopic(testid) {

				let that = this;
				uni.request({
					url: gData.site_url + 'Tests.GetTopic',
					method: 'GET',
					data: {
						// uid token testsid type 0新做/重做 1继续做 2只看错误 3看全部
						'uid': that.userinfo.id,
						'token': that.userinfo.token,
						'testsid': testid,
						'type': type
					},
					success: res => {
						console.log(res);
						if (res.data.data.code != '0') {

							return;
						}
						this.tiList = res.data.data.info[0].list;


					}
				});

			},
			// 添加/取消错题本
			addCuotiben(testid, qid) {

				let gData = app.globalData;
				let that = this;
				let ans = {
					"rs": that.user_ans[that.currentIndex]
				};

				uni.request({
					url: gData.site_url + 'Wrongbook.SetWrong',
					method: 'GET',
					data: {
						// uid token type 3收藏中添加错题本 testsid qid status 1添加 2删除
						'uid': that.userinfo.id,
						'token': that.userinfo.token,
						'type': 3,
						'testsid': testid,
						'qid': qid,
						'ans': ans,
						'status': '1'
					},
					success: res => {
						uni.showToast({
							icon: 'none',
							title: res.data.data.msg
						});

						if (res.data.data.code == '0') {
							that.cuotibenData[that.currentIndex] = true;
							that.$set(that.cuotibenData, that.currentIndex, true);
						}

					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});

			},
			// 根据题号切换题目
			checkByTihao(index) {
				this.currentIndex = index;
				this.currentQid = parseInt(index) + 1;
				let type = this.tiList[this.currentIndex].type;
				if (type == '4') {
					this.getAnswerStr(this.currentIndex);
				}
				this.showMsk = true;
			},
			getAnswerStr(current) {
				let that = this;
				let tiList = that.tiList;
				if (tiList[current].type == '2') {
					let rs_arr = (tiList[current].answer.rs).split(',');
					let rs_str = '';
					for (let current in rs_arr) {
						rs_str += that.ti_info_item_abc[rs_arr[current]] + ', ';
					}
					that.dingxiang.right_ans[current] = rs_str.substring(0, rs_str.lastIndexOf(','));
				} else if (tiList[current].type == '5') {
					let rs_arr = (tiList[current].answer.rs).split(',');
					let rs_str = '';
					for (let current in rs_arr) {
						rs_str += that.ti_info_item_abc[rs_arr[current]] + ', ';
					}
					that.budingxiang.right_ans[current] = rs_str.substring(0, rs_str.lastIndexOf(','));
				} else if (tiList[current].type == '4') {
					// 填空题用户答案打散成数组
					that.tiankong.ans[current] = (tiList[current].rs_user.rs).split(',');
					that.tiankong.right_ans[current] = (tiList[current].answer.ans);
				}

			},

			// 关闭遮罩上层
			closezhetop() {
				this.is_zhe_top = '0';
				this.is_zhe_show = '0';
				this.strWindow = !this.strWindow;
			},
			// 弹出遮罩
			openpicker() {
				this.is_zhe_show = '1';
				this.is_zhe_top = '1';
				this.strWindow = !this.strWindow;
				console.log(this.right_cuo);
			},
			// 收藏取消题目 
			storeQuestion(testid, qid) {

				let gData = app.globalData;
				let that = this;

				let type = '';
				if (that.storeData[that.currentQid] !== true) {
					type = 1;
				} else {
					type = 2;
				}

				uni.request({
					url: gData.site_url + 'Topic.SetFav',
					method: 'GET',
					data: {
						// uid token testid qid type 1添加 2取消 
						'uid': that.userinfo.id,
						'token': that.userinfo.token,
						'testid': testid,
						'qid': qid,
						'type': type
					},
					success: res => {
						uni.showToast({
							icon: 'none',
							title: res.data.data.msg
						});

						if (res.data.data.code == '0') {
							that.$set(that.storeData, that.currentIndex, !that.storeData[that.currentIndex]);
						}

					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					}

				});
			},
			/**
			 * 获取对错号图片显示位置
			 * @param {Object} type 题目类型
			 * @param {Object} rs 答案选项标志
			 */
			getDuiCuoFlag(type, flag) {
				let rs = this.tiList[this.currentIndex].answer.rs;
				if (type == '0') {
					//判断题 rs 1正确 0错误
					if (flag == rs) { //答案和正确答案相同
						return this.duihao;
					} else {
						return this.cuohao;
					}
				}
				// 选择题
				else if (type == '1') {
					// 如果选项等于当前答案, 即返回对号
					if (flag == this.tiList[this.currentIndex].answer.rs) {
						return this.duihao;
					} else {
						return this.cuohao;
					}
				}

			},
			/**
			 * 处理题目选项的文字颜色 判断单选 
			 * @param {Object} type
			 */
			setAnsTxtColor(type) {
				let rs = this.tiList[this.currentIndex].answer.rs;
				if (type == '0') {
					if (rs == '1') {
						this.panduan.a_class = 'green';
						this.panduan.b_class = 'red';
					} else if (rs == '0') {
						this.panduan.a_class = 'red';
						this.panduan.b_class = 'green';
					}
				} else if (type == '1') {
					//单选
					for (let idx in this.ans_txt_class) {
						if (idx == rs) {
							this.ans_txt_class[idx] = 'green';
							continue;
						}
						this.ans_txt_class[idx] = 'red';
					}
				}

			},
			/* 处理判断题
			 * @param {Object} rs 正确答案
			 * @param {Object} us 用户点击的答案
			 */
			checkPanduan(rs, us) {
				this.panduan.is_end = '1'; //已做完此题
				this.setAnsTxtColor('0'); //答案选项文字颜色
				this.setAnsCache(rs, us);
			},
			/* 处理单选题
			 * @param {Object} rs 答案
			 * @param {Object} us 用户所选的索引
			 */
			checkDanxuan(rs, us) {
				this.setAnsTxtColor('1');
				this.setAnsCache(rs, us);
			},
			/* 处理定项选择题(必须全部答案都选中才算做对)
			 * @param {Object} rs 答案字符串
			 * @param {Object} us 用户所选索引
			 */
			checkDingxiang(rs, us) {

				us = us.toString();
				let usr_ans = this.dingxiang.ans;

				if (usr_ans.indexOf(us) != '-1') {
					this.dingxiang.ans.splice(usr_ans.indexOf(us), 1);
					this.dingxiang.abc[us].is_checked = '';
				} else {
					this.dingxiang.ans.push(us);
					this.dingxiang.abc[us].is_checked = '1';
				}

				this.dingxiang.is_start_check = '1';
			},
			// 提交定项答案
			subDingXiang() {
				if (this.dingxiang.ans.length == 0) {
					uni.showToast({
						icon: 'none',
						title: '请选择答案'
					});
					return;
				}
				this.dingxiang.is_sub = '1';
				this.setAnsCache(this.tiList[this.currentIndex].answer.rs, this.dingxiang.ans, '2');
			},
			// 提交填空题
			subTiankong() {
				let tiankong_ans = this.tiankong.ans;
				for (let idx in tiankong_ans) {
					if (tiankong_ans[idx] == '') {
						uni.showToast({
							icon: 'none',
							title: '答案不能为空'
						});
						return;
					}
				}
				let currentId = this.tiList[this.currentIndex].id;
				this.tiankong.sub[currentId] = '1';
				this.setAnsCache(this.tiList[this.currentIndex].answer.ans, this.tiankong.ans, '4');
			},
			// 处理不定项选择题 rs 正确答案字符串 us用户所选答案索引
			checkBudingXiang(rs, us) {
				for (let idx in this.budingxiang.key) {
					this.budingxiang.key[idx]++;
				}
				let usr_ans = this.budingxiang.ans;
				let currentId = this.tiList[this.currentIndex].id;

				if (usr_ans.indexOf(us) != '-1') {
					this.budingxiang.ans.splice(usr_ans.indexOf(us), 1);
					this.$set(this.budingxiang.abc[currentId], us, {
						'is_checked': ''
					});
				} else {
					this.budingxiang.ans.push(us);
					this.$set(this.budingxiang.abc[currentId], us, {
						'is_checked': '1'
					});
				}

				this.budingxiang.is_start_check = '1';
			},
			//提交不定项答案
			subBudingXiang() {
				if (this.budingxiang.ans.length == 0) {
					uni.showToast({
						icon: 'none',
						title: '请选择答案'
					});
					return;
				}

				let currentId = this.tiList[this.currentIndex].id;
				this.budingxiang.sub[currentId] = '1';
				this.setAnsCache(this.tiList[this.currentIndex].answer.rs, this.budingxiang.ans, '5');
			},
			//处理用户答题, 并存入缓存
			setAnsCache(rs, us, type = '') {
				let that = this;
				let gData = app.globalData;
				//获取此题缓存 如果已做过, 中断执行
				if ((that.ansCache != null) && (that.ansCache.ti_data[that.currentIndex] == 'is_end')) {
					return;
				}

				that.user_answer.ti_data[that.currentIndex] = 'is_end';
				// 判断和单选共用一个逻辑 	
				if (type == '') {
					if (rs == us) {
						that.user_answer.dui_num++; // 用户做对了 
						that.right_cuo[that.currentIndex] = '1';
						that.$set(that.right_cuo, that.currentIndex, '1');
					} else {
						that.user_answer.cuo_num++; //用户做错了 A
						that.right_cuo[that.currentIndex] = '0';
						that.$set(that.right_cuo, that.currentIndex, '0');
					}
					// 存放用户答案 
					that.user_ans[that.currentIndex] = us;
				}
				// 定项多选择
				else if (type == '2') {
					// rs此时是字符串 us是用户选择的选项数组
					let rs_arr = rs.split(',');
					let same_num = 0;

					for (let idx1 in rs_arr) {
						//拼接正确答案字符串, 供显示用
						if (that.ti_info_item_abc[rs_arr[idx1]] != null) {
							that.dingxiang.right_ans += that.ti_info_item_abc[rs_arr[idx1]] + ' ';
						}
						for (let idx2 in us) {
							if (rs_arr[idx1] == us[idx2]) {
								same_num++;
							}
						}
					}

					if ((same_num == rs_arr.length) && (rs_arr.length == us.length)) {
						// 正确
						that.user_answer.ti_data[that.currentIndex] = 'is_end';
						that.user_answer.dui_num++;
						that.right_cuo[that.currentIndex] = '1';
					} else {
						that.user_answer.cuo_num++;
						that.right_cuo[that.currentIndex] = '0';
					}

					// 存放答案
					that.user_ans[that.currentIndex] = us.join(',');
				}
				// 填空题
				else if (type == '4') {
					that.user_answer.ti_data[that.currentIndex] = 'is_end';
					// 此时rs是数组 us也是数组
					let dui_num = 0;
					that.tiankong.right_ans[that.currentIndex] = '';
					for (var i = 0; i < rs.length; i++) {
						// 拼接正确答案字符串
						that.tiankong.right_ans[that.currentIndex] += (i + 1) + '.' + rs[i][0] + ' ';
						if (rs[i][0] == us[i]) {
							dui_num++;
						}
					}
					if (dui_num == rs.length) {
						// 全对
						that.user_answer.dui_num++;
						that.right_cuo[that.currentIndex] = '1';
					} else {
						that.user_answer.cuo_num++;
						that.right_cuo[that.currentIndex] = '0';
					}

					// 存放答案
					that.user_ans[that.currentIndex] = us.join(',');

				}
				// 不定项多选(逻辑和定项多选相同)
				else if (type == '5') {
					// rs此时是字符串 us是用户选择的选项数组
					let rs_arr = rs.split(',');
					let same_num = 0;
					that.budingxiang.right_ans[that.currentIndex] = '';
					for (let idx1 in rs_arr) {
						//拼接正确答案字符串, 供显示用
						if (that.ti_info_item_abc[rs_arr[idx1]] != null) {
							that.budingxiang.right_ans[that.currentIndex] += that.ti_info_item_abc[rs_arr[idx1]] + ' ';
						}
						for (let idx2 in us) {
							if (rs_arr[idx1] == us[idx2]) {
								same_num++;
							}
						}
					}

					if ((same_num == rs_arr.length) && (rs_arr.length == us.length)) {
						// 正确
						that.user_answer.ti_data[that.currentIndex] = 'is_end';
						that.user_answer.dui_num++;
						that.right_cuo[that.currentIndex] = '1';
					} else {
						that.user_answer.cuo_num++;
						that.right_cuo[that.currentIndex] = '0';
					}

					// 存放答案
					that.user_ans[that.currentIndex] = us.join(',');

				}

				// 用户答题情况存入缓存
				uni.setStorageSync('user_answer', that.user_answer);

			},
			// 获取收藏题目列表
			getStoreList() {
				let gData = app.globalData;
				let that = this;
				uni.request({
					url: gData.site_url + 'Topic.GetFavList',
					method: 'GET',
					data: {
						/****************** 为调试方便 接口暂时不取token***************/
						// uid token classids
						'uid': that.userinfo.id,
						'token': that.userinfo.token,
					},
					success: res => {
						console.log(res);
						if (res.data.data.code != '0') {
							return;
						}
						if (res.data.data.info.length == 0) {
							console.log('fuck');
							this.kongkong = true;
						} else {
							this.kongkong = false;
						}


						that.tiList = res.data.data.info;
						that.ti_num = that.tiList.length;
						//声明做题结果统计数组
						that.user_answer.ti_data = new Array(that.ti_num);

						// 声明定项和不定项的选项数组 记录选择项 防止重复
						that.budingxiang.abc = new Array();
						let tiList = that.tiList;
						console.log(tiList);
						for (let idx in tiList) {
							if (tiList[idx].type != '5') {
								continue;
							}
							that.budingxiang.abc[tiList[idx].id] = [{
								'is_checked': ''
							}, {
								'is_checked': ''
							}, {
								'is_checked': ''
							}, {
								'is_checked': ''
							}, {
								'is_checked': ''
							}];
						}

						// 渲染收藏星星
						let storeList = that.tiList;
						for (let idx in storeList) {
							that.storeData[idx] = true;
							that.$set(that.storeData, idx, true);
						}

					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			back() {
				uni.navigateBack({
					delta: 1
				});
			},
			handletouchmove: function(event) {
				// console.log(event)
				if (this.flag !== 0) {
					return;
				}
				let currentX = event.changedTouches[0].pageX;
				let tx = currentX - this.lastX;
				let text = '';
				this.mindex = -1;
				//左右方向滑动
				if (tx < 0) {
					text = '向左滑动';
					this.flag = 1;
					//切换下一题
					if (this.currentIndex == (this.tiList.length - 1)) {
						return;
					}
					this.currentIndex++;
					this.currentQid++;

				} else if (tx > 0) {
					text = '向右滑动';
					this.flag = 2;
					// 切换上一题
					if (this.currentIndex == '0') {
						return;
					}
					this.currentIndex--;
					this.currentQid--;
				}

				//将当前坐标进行保存以进行下一次计算
				this.lastX = currentX;
				this.text = text;
			},
			handletouchstart: function(event) {
				// console.log(event)
				this.lastX = event.changedTouches[0].pageX;
			},
			handletouchend: function(event) {
				this.flag = 0;
				this.text = '没有滑动';
			},


		}
	}
</script>

